.tubus-price {
    overflow: hidden;
    background: rgba(247, 82, 65, 0.70);

    .container {
        padding: 0 142px 100px 148px;

        @media (max-width: 1860px) {
            padding: 0 70px 70px 70px;
            }

        @media (max-width: 1200px) {
        padding: 0 50px 70px 50px;
        }
    }

    .packing {
        &__wrapper {
            width: 885px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            margin: 0 0 48px 0;
            padding-top: 73px;

            @media (max-width: 1200px) {
                padding-top: 40px;
                margin: 0 0 30px 0;
            }

            @media (max-width: 860px) {
                width: 100%;
            }
        }

        &__title {
            font-size: 46px;
            font-weight: 900;
            line-height: 42px;
            margin-bottom: 28px;
            color: $white;

            @media (max-width: 1200px) {
                font-size: 42px;
                line-height: 38px;
                margin-bottom: 15px;
            }

            @media (max-width: 860px) {
                br {
                    display: none;
                }
            }
        }

        &__description {
            font-size: 28px;
            font-style: normal;
            font-weight: 400;
            line-height: 34px;

            @media (max-width: 1200px) {
                font-size: 24px;
            }

            @media (max-width: 860px) {
                width: 100%;
                font-size: 21px;
                line-height: 28px;
            }
        }

        .tubus-slider {
            visibility: visible;
            position: relative;
            margin-bottom: 100px;
            margin-bottom: 69px;

            @media (max-width: 1200px) {
                margin-bottom: 50px;
            }
    
            .slider-list {
                overflow: inherit;
                width: 1690px;
                height: 550px;
    
                .slider-track {
                    height: 100%;
                }
    
                .slide {
                    width: auto;
                    border: none;

                    .slide-card {
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;
                        height: 100%;
                        align-items: center;
                        width: 370px;
                        border: none;
                        border-radius: 14px;
                        line-height: 0;
                        overflow: hidden;
                        background-color: $white;
                        border-radius: 32px;
                        box-shadow: 12px 12px 24px 0px rgba(97, 27, 20, 0.32);

                        @media (max-width: 1200px) {
                            width: 350px;
                        }

                        img {
                            @media (max-width: 860px) {
                                height: 144px;
                            }
                        }

                        &__wrapper {
                            display: flex;
                            flex-direction: column;
                            align-items: center;
                            justify-content: space-between;
                            max-height: 241px;
                        }
            
                        &__description {
                            display: flex;
                            flex-direction: column;
                            justify-content: space-between;
                            width: 100%;
                            height: 100%;
                            text-align: center;
                            padding: 0 25px 0px 25px;
                        }
        
                        &__name {
                            font-size: 19px;
                            line-height: 23px;
                            font-weight: 700;
                            color: $black;

                            @media (max-width: 1200px) {
                                font-size: 15px;
                            }
                        }

                        &__features {
                            font-size: 14px;
                            font-weight: 600;
                            line-height: normal;
                            text-transform: uppercase;
                            color: #EF3C15;
                        }

                        &__count {
                            font-size: 14px;
                            line-height: normal;
                            font-weight: 400;
                            text-align: center;
                            color: $black;
                        }

                        &__calculation {
                            font-size: 12px;
                            font-weight: 300;
                            line-height: normal;
                            color: #000;
                        }

                        &__ellipse-wrapper {
                            display: flex;
                            flex-direction: row;
                            justify-content: space-between;
                            width: 30px;
                        }

                        &__ellipse {
                            display: block;
                            width: 6px;
                            height: 6px;
                            border-radius: 50%;
                            background-color: #EF3C15;
                        }

                        &__provide {
                            text-align: center;
                            //margin-bottom: 20px;

                            p {
                                position: relative;
                                font-size: 16px;
                                line-height: 19px;
                                text-transform: uppercase;
                                padding-right: 28px;
                                color: #EF3C15;

                                &::after {
                                    position: absolute;
                                    content: "";
                                    display: block;
                                    width: 20px;
                                    height: 20px;
                                    top: 0;
                                    right: 0;
                                    background-image: url("../img/main/tubus-slider/info.svg");
                                    background-size: contain;
                                    background-repeat: no-repeat;
                                }
                            }
                        }

                        &__button-wrapper {
                            width: 100%;
                        }

                        &__buy-button {
                            display: block;
                            width: 330px;
                            padding: 10px 0;
                            margin: 0 auto;
                            color: #01B489;
                            font-size: 18px;
                            font-weight: 600;
                            line-height: normal;
                            background: #FFF;
                            border: 1px solid #01B489;
                            border-radius: 32px;
                            margin-bottom: 10px;
                            cursor: pointer;
                            transition: all 500ms ease;

                            &:hover {
                                background-color: #01B489;
                                color: $white;
                                transition: all 500ms ease;
                            }
                        }

                        &__add__button {
                            display: block;
                            width: 330px;
                            padding: 10px 0;
                            margin: 0 auto;
                            color: $white;
                            font-size: 18px;
                            font-weight: 600;
                            line-height: normal;
                            background: #EF3C15;
                            border: none;
                            border-radius: 32px;
                            margin: 0 auto 36px;
                            cursor: pointer;
                            transition: all 500ms ease;

                            &:hover {
                                box-shadow: 0px 4px 24px 0px $red;
                                transition: all 500ms ease;
                            }
                        }
                    }
                }
            }
    
            .slider-arrows {
                position: absolute;
                top: -106px;
                right: -52px;
                display: flex;
                justify-content: space-between;
                width: 130px;
                flex-direction: row;
    
                .prev {
                    display: block;
                    width: 40px;
                    height: 40px;
                    background-image: url("../img/main/industry-slider/arrow-circle.svg");
                    background-size: contain;
                    background-repeat: no-repeat;
                    border-radius: 50%;
                    background-color: $white;
                    box-shadow: 12px 12px 24px 0px rgba(97, 27, 20, 0.32);
                }
    
                .next {
                    display: block;
                    width: 40px;
                    height: 40px;
                    background-image: url("../img/main/industry-slider/arrow-circle.svg");
                    transform: rotate(180deg);
                    background-size: contain;
                    background-repeat: no-repeat;
                    border-radius: 50%;
                    background-color: $white;
                    box-shadow: 12px 12px 24px 0px rgba(97, 27, 20, 0.32);
                }

                @media (max-width: 860px) {
                    position: absolute;
                    top: 227px;
                    right: -10px;
                    width: 100%;
                    z-index: 4;
        
                    .prev {
                        width: 55px;
                        height: 55px;
                    }
        
                    .next {
                        display: block;
                        width: 55px;
                        height: 55px;
                    }
                }
            }
        }

        .tubus-cardlist {
            display: none;
            display: flex;
            flex-direction: row;
            justify-content: center;
            flex-wrap: wrap;
            width: auto;
            gap: 1.58vw;
            margin: 0 auto;
            margin-bottom: 60px;

