                        font-size: 15px;
                    }
                }

                &__features {
                    font-size: 14px;
                    font-weight: 600;
                    line-height: normal;
                    text-transform: uppercase;
                    color: #EF3C15;
                    //margin-bottom: 20px;
                }

                &__count {
                    font-size: 14px;
                    line-height: normal;
                    font-weight: 400;
                    text-align: center;
                    color: $black;
                }

                &__calculation-wrapper {
                    //margin-bottom: 5px;
                }

                &__calculation {
                    font-size: 12px;
                    font-weight: 300;
                    line-height: normal;
                    color: #000;
                }

                &__ellipse-wrapper {
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    width: 30px;
                    //margin-bottom: 20px;
                }

                &__ellipse {
                    display: block;
                    width: 6px;
                    height: 6px;
                    border-radius: 50%;
                    background-color: #EF3C15;
                }

                &__provide {
                    text-align: center;
                    //margin-bottom: 20px;

                    p {
                        position: relative;
                        font-size: 16px;
                        line-height: 19px;
                        text-transform: uppercase;
                        padding-right: 28px;
                        color: #EF3C15;

                        &::after {
                            position: absolute;
                            content: "";
                            display: block;
                            width: 20px;
                            height: 20px;
                            top: 0;
                            right: 0;
                            background-image: url("../img/main/tubus-slider/info.svg");
                            background-size: contain;
                            background-repeat: no-repeat;
                        }
                    }
                }

                &__button-wrapper {
                    width: 100%;
                }

                &__buy-button {
                    display: block;
                    width: 330px;
                    padding: 10px 0;
                    margin: 0 auto;
                    color: #01B489;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: normal;
                    background: #FFF;
                    border: 1px solid #01B489;
                    border-radius: 32px;
                    margin-bottom: 10px;
                }

                &__add__button {
                    display: block;
                    width: 330px;
                    padding: 10px 0;
                    margin: 0 auto;
                    color: $white;
                    font-size: 18px;
                    font-weight: 600;
                    line-height: normal;
                    background: #EF3C15;
                    border: none;
                    border-radius: 32px;
                    margin: 0 auto 36px;
                }
            }
        }
        
        .slick-slider{
            width: 1300px;
            margin-bottom: 47px;
        }
        
        /* Лента слайдов */
        .slick-track{
            display: flex;
        }
        /* Слайд */
        .slick-slide,
        .slick-current,
        .slick-active {
            margin-right: 30px;
        }

        &__tab {
            display: block;
            padding: 7px 18px;
            border-radius: 24px;
            border: 1px solid rgba(239, 60, 21, 0.30);
            background: $white;
            box-shadow: 12px 12px 24px 0px rgba(97, 27, 20, 0.32);
            font-size: 20px;
            font-weight: 600;
            line-height: normal;
            color: #EF3C15;
            cursor: pointer;
        }

        .active-tab {
            border: 3px solid $white;
            background-color: #EF3C15;
            color: $white;
        }

        &__showing-link {
            display: block;
            width: 400px;
            padding: 20px 48px;
            border-radius: 32px;
            background: $white;
            font-size: 24px;
            font-style: normal;
            font-weight: 600;
            line-height: normal;
            color: #EF3C15;
            text-align: center;
            margin: 0 auto;
            border: none;
            cursor: pointer;
            transition: all 500ms ease;

            &:hover {
                color: $red;
                box-shadow: 5px 5px 24px 5px rgba(239, 60, 21, 0.32);
                transition: all 500ms ease;
              }

            @media (max-width: 1200px) {
                width: 430px;
                padding: 15px 35px;
                font-size: 21px;
            }
        }
    }
}

